import Vue from 'vue'
import {
	mapState
} from 'vuex';
export default {
	data() {
		return {
			selectIndex: 0,
			swiper_height: 700,
			showToast: false,
			toastContent: '',
			loading: false,
		}
	},
	computed: {
		...mapState({
			user: state => state.AuthUser,
		}),
	},
	mounted() {
		this.getHeight();
		this.loading = true;
		setTimeout(() => {
			this.loading = false;
		}, 300)
	},
	methods: {
		getHeight() {
			let that = this;
			let height = 0;
			uni.getSystemInfo({
				success(res) {
					that.screen_height = res.windowHeight;
					that.screen_width = res.windowWidth;
					let otherHeight = 0;
					let query = uni.createSelectorQuery().in(that);
					query.select('#swiper').boundingClientRect(res => {
						that.swiper_height = that.screen_height - res.top - 80;
					}).exec();
				}
			});
		},
		hideModal(e) {
			this.showToast = false;
		},
		buttonSelect(e) {
			this.selectIndex = e.currentTarget.dataset.id;
			switch (this.selectIndex) {
				case 0:
					if (this.user.authentication == '' || this.user.authentication == null) {
						this.toastContent = '先去认证身份，再进行发布操作～～'
						this.showToast = true;
						return;
					}
					uni.navigateTo({
						url: '/pages/addition-article/addition-article'
					});
					break;
				case 1:
					if (this.user.user_organization == '' || this.user.user_organization == null) {
						this.toastContent = '只有学生组织才可发布活动，快去认证吧～～'
						this.showToast = true;
						return;
					}
					uni.navigateTo({
						url: '/pages/addition-activity/addition-activity'
					});
					break;
				case 2:
					if (this.user.authentication == '' || this.user.authentication == null) {
						this.toastContent = '先去认证身份，再进行发布操作～～'
						this.showToast = true;
						return;
					}
					uni.navigateTo({
						url: '/pages/addition-commodity/addition-commodity'
					});
					break;
				case 3:
					if (this.user.authentication == '' || this.user.authentication == null) {
						this.toastContent = '先去认证身份，再进行发布操作～～'
						this.showToast = true;
						return;
					}
					uni.navigateTo({
						url: '/pages/addition-recruit/addition-recruit'
					});
					break;
			}
		}
	}
}
